%vert-align {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

%flex-hor-vert-center {
  display: flex;
  justify-content: center;
  align-items: center;}

%borrad50 {
  border-radius: 50%;
}


%transition {
  transition: 0.2s linear;
}

%flex-align-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

%borrad05 {
  border-radius: 0.5rem;
}

%btn {
  cursor: pointer;
  border: none;
  outline: none;
}